import { Directive, ElementRef, HostListener, HostBinding } from '@angular/core';

@Directive({
  selector: '[appFullpage]'
})
export class FullpageDirective {

  @HostBinding('class.my-element') private _addOverflow = false;

  constructor(private element: ElementRef) {}

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    console.log('resize');
    this.resize();
  }

  @HostListener('load', ['$event'])
  onLoad(event) {
    console.log('resize');
    this.resize();
  }

  resize() {
    // We listen to the resize event
    // We execute the same script as before
    const vh = window.screen.height * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    const bgwidth = this.element.nativeElement.width;
    const bgheight = this.element.nativeElement.height;
    console.log({ bgheight: bgheight });
    const winwidth = window.innerWidth;
    const winheight = window.screen.height;
    const widthratio = winwidth / bgwidth;
    const heightratio = (winheight / bgheight) ;
    console.log({ winheight: winheight });

    const widthdiff = heightratio * bgwidth;
    const heightdiff =  (winheight * 40) / 100;
    console.log({ heightdiff: heightdiff });
    //  if (heightdiff > winheight) {*
    // this.element.nativeElement.width = winwidth;

    document
      .getElementById("cta")
      .style.setProperty("height", `${heightdiff}px`);
    //document.getElementById("cta").style.setProperty; "--max-heigth", `35%`;
    //this.element.nativeElement.minHeight = heightdiff;

    this.element.nativeElement.style.setProperty("height", `${heightdiff}px`);

    // } else {
    // this.element.nativeElement.width = widthdiff;
    // this.element.nativeElement.height = winheight;
    //}
  }
}
