import { Component, OnInit } from '@angular/core';
import { ContentService } from '../shared/services/content.service';
import { trigger, stagger, animate, style, group, query as q, transition, keyframes } from '@angular/animations';
const query = (s, a, o = { optional: true }) => q(s, a, o);

export const homeTransition = trigger("homeTransition", [
         transition(":enter", [
           q(".pinned-trip", style({ opacity: 0 })),
           q(
             ".pinned-trip",
             stagger(300, [
               style({ transform: "translateY(100px)" }),
               animate(
                 "1s cubic-bezier(.75,-0.48,.26,1.52)",
                 style({ transform: "translateY(0px)", opacity: 1 })
               )
             ])
           )
         ]),
         transition(":leave", [
           q(
             ".pinned-trip",
             stagger(300, [
               style({ transform: "translateY(0px)", opacity: 1 }),
               animate(
                 "1s cubic-bezier(.75,-0.48,.26,1.52)",
                 style({ transform: "translateY(100px)", opacity: 0 })
               )
             ])
           )
         ])
       ]);

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.css'],
  animations: [homeTransition],
  host: {
    '[@homeTransition]': ''
  }
})
export class PageComponent implements OnInit {
  page = {
    title: 'Home',
    maincontent: 'assets/home.png',
    logo: 'assets/header.png',
    image: 'assets/bg_0.jpg'
  };
  title = 'mobile';
  constructor(private contentService: ContentService) {}

  ngOnInit() {
    this.page = this.contentService.pages['home'];
    // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
    const vh = window.innerHeight * 0.01;
    // Then we set the value in the --vh custom property to the root of the document
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  }
}
